body{
  background-color: black;
  font: 16px "Helvetica Neue",Helvetica,Arial,sans-serif;
  color: rgb(185, 185, 185);
  font-size: 18px;
  margin-top: 130px;
}

a:-webkit-any-link {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

h1{
  letter-spacing: -2px;
  text-shadow: 2px 2px #708c8d;
  color: white;
}

h2{
  margin-top: 100px;
  color: white;
  text-align: center;

}

h3{
  padding: 12px;
  margin-top: 40px;
  color:white;
}


.active-button {
    color: rgb(143, 81, 81);
  }

.homemargin{
  margin-top: 110px;
}

.text-allign{
  text-align: center;
  position: relative;
  z-index: 99;
}

.title{
  font-size: 25px;
  color: rgb(255, 255, 255);
  letter-spacing: -1;

}


.ppicture{
  width: 125px;
  height: 125px;
  border-radius: 100%;
}

.home-nav{
  font-size: 25px;
  font-family: Arial, Helvetica, sans-serif;
  padding: 35px 15px;
  margin: 0;
  text-shadow: 1px 1px #574c4c;
  word-spacing: 43px;
  color: rgb(17, 0, 255);
  margin-top: 35px;
}

.home-nav a:hover{
  color: rgb(37, 30, 138);
}


.other-nav{
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  position: absolute;
  right: 0;
  top: 0;
  padding: 10px 15px;
  margin: 0;
  text-shadow: 1px 1px #574c4c;
  word-spacing: 15px;
  color: rgb(17, 0, 255);

}

.fa {
  padding: 10px;
  font-size: 25px;
  width: 50px;
  text-align: center;
  text-decoration: none;
}

.fa:hover {
  opacity: 0.7;
  color: rgb(17, 0, 255);
}

.about {
  line-height: 1.4;
  margin: 0 auto;
  width: 50%;
  max-width: 750px;
}
.about-text{
  border-top: 1px solid #222;
}

.project-links{
  text-decoration: none;
  color: white;
}

.project-links:hover{
  opacity: 0.7;
  color: rgb(55, 108, 255);
}

.footer {
  margin: 0 auto;
  bottom: auto;
  margin-top: 100px;
  color: rgb(104, 104, 104);
  text-align: center;
  border-top: 1px solid #222;
}



.postimage{
  margin: 0 auto;
  padding: 10px;
  width: 720px;
  height: auto;
}






.projects{
  margin-top: 40px;
  margin-left: 15%;
  margin-right: 15%;
}




.gallery {
  margin: 0 auto;
  margin-top: 25px;
  border: 1px solid rgb(112, 112, 112);
  width: 550px;
}

.gallery:hover {
  border: 1px solid rgb(58, 58, 58);
}

.gallery img {
  width: 100%;
  height: auto;
}

.desc {
  padding: 15px;
  text-align: center;
}


.borderbot {
  border-left: 1px solid #222222;
  position: absolute;
  left: 50%;
  bottom: 0;
}